*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
}

:root {
  --primary: #6a59ff;
  --white: #ffffff;
  --bg: #f5f5f5;
}

html {
  /* font-size: 62.5%; */
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 1.3rem;
}

::-webkit-scrollbar-thumb {
  border-radius: 1rem;
  background: #797979;
  transition: all 0.5s ease-in-out;
}

::-webkit-scrollbar-thumb:hover {
  background: #222224;
}

::-webkit-scrollbar-track {
  background: #f9f9f9;
}

body {
  /* font-size: 1.6rem; */
  background: var(--bg);
}

.spicessss-container {
  max-width: 65rem;
  padding: 2rem 0.5rem;
  margin: 0 auto;
  border-radius: 20px;
  background-image: linear-gradient(rgb(231, 212, 235), rgb(152, 211, 23));
}

.heading {
  padding: 1rem 0;
  font-size: 3.5rem;
  text-align: center;
}

.swiper_container {
  height: 33rem;
  padding: 2rem 0;
  position: relative;
}

.swiper-slide {
  width: 20rem;
  height: 25rem;
  position: relative;
}

.swiper-slide img {
  width: 20rem;
  height: 25rem;
  border-radius: 2rem;
  object-fit: cover;
}

.swiper-slide-shadow-left,
.swiper-slide-shadow-right {
  display: none;
}

.slider-controler {
  position: relative;
  bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slider-controler .swiper-button-next {
  left: 62% !important;
  transform: translateX(-58%) !important;
}
.slider-controler .swiper-button-next:hover {
  background-color: red;
}

.slider-controler .slider-arrow {
  background: var(--white);
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  left: 38%;
  transform: translateX(-40%);
  filter: drop-shadow(0px 8px 24px rgba(18, 28, 53, 0.1));
}
.slider-controler .slider-arrow:hover {
  background-color: red;
}

.slider-controler .slider-arrow ion-icon {
  font-size: 1.8rem;
  color: #222224;
}

.slider-controler .slider-arrow::after {
  content: '';
}

.swiper-pagination {
  position: relative;
  width: 15rem !important;
  bottom: 1rem;
}

.swiper-pagination .swiper-pagination-bullet {
  filter: drop-shadow(0px 8px 24px rgba(18, 28, 53, 0.1));
}

.swiper-pagination .swiper-pagination-bullet-active {
  background: var(--primary);
}

@media (min-width: 1440px) {
  html {
    zoom: 1.5;
  }
}

@media (min-width: 2560px) {
  html {
    zoom: 1.7;
  }
}

@media (min-width: 3860px) {
  html {
    zoom: 2.5;
  }
}

@media (max-width: 500px) {
  .swiper_container {
    height: 47rem;
  }
  .swiper-slide {
    width: 28rem !important;
    height: 36rem !important;
  }
  .swiper-slide img {
    width: 28rem !important;
    height: 36rem !important;
  }
}

@media (max-width: 990px) {
  .slider-controler .swiper-button-next {
    left: 70% !important;
    transform: translateX(-70%) !important;
  }
}

@media (max-width: 450px) {
  .slider-controler .swiper-button-next {
    left: 80% !important;
    transform: translateX(-80%) !important;
  }
}

@media (max-width: 990px) {
  .slider-controler .swiper-button-prev {
    left: 30% !important;
    transform: translateX(-30%) !important;
  }
}

@media (max-width: 450px) {
  .slider-controler .swiper-button-prev {
    left: 10% !important;
    transform: translateX(-10%) !important;
  }
}
@media (max-width: 768px) {
  .spicessss-container {
    max-width: 85rem;
    padding: 1rem 0.5rem;
    margin: 0 auto;
    border-radius: 20px;
    background-image: linear-gradient(rgb(231, 212, 235), rgb(152, 211, 23));
  }
  .swiper_container {
    height: 20rem;
    padding: 2rem 0;
    position: relative;
  }

  .swiper-slide {
    width: 20rem;
    height: 25rem;
    position: relative;
  }

  .swiper-slide img {
    width: 30rem;
    height: 15rem;
    border-radius: 2rem;
    object-fit: cover;
  }
}
